* {
  border-bottom: none !important;
}

.navbar {
  background-color: rgb(0, 0, 0);
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  z-index: 1000;
  text-decoration: none;
  outline: none;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
  z-index: 1000;
  text-decoration: none;
  outline: none;
}

.nav-menu {
  background-color: #000000;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  z-index: 1000;
  text-decoration: none;
  outline: none;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
  z-index: 1000;
  text-decoration: none;
  outline: none;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
  z-index: 1000;
  text-decoration: none;
  outline: none;
}

.nav-text a {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
  z-index: 1000;
  text-decoration: none;
  outline: none;
}

.nav-text a:hover {
  background-color: rgb(255, 255, 255);
  color: #000000;
  z-index: 1000;
  text-decoration: none;
  outline: none;
}

.nav-menu-items {
  width: 100%;
  z-index: 1000;
  text-decoration: none;
  outline: none;
}

.navbar-toggle {
  background-color: #000000;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  z-index: 1000;
  text-decoration: none;
  outline: none;
}

span {
  margin-left: 16px;
  z-index: 1000;
  text-decoration: none;
  outline: none;
}



#tg {
  width: 50px;
  height: 50px;
  position: absolute;
  right: 1rem;
}
